.castList {
  display: flex;
  box-sizing: border-box;
  padding-left: 0;

  flex-wrap: wrap;
  gap: 30px;

  justify-content: center;
  align-content: center;
  list-style: none;
}

.castItem {
  width: 200px;
}

.castDesc {
  color: black;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 14px;
}

.castImg {
  display: block;

  max-width: 100%;
  height: auto;

  object-fit: cover;
  object-position: center;
}
